.search-container {
  align-items: center;
  display: flex;
}

.ui.fluid.dropdown {
  background-color: #000;
  border: 1px solid #fff3;
  border-radius: 3px;
  flex-grow: 1;
  width: auto;
  min-height: 33px;
  transition: border .2s;
}

.ui.multiple.dropdown {
  padding: 0;
}

.ui.fluid.dropdown:hover, .ui.fluid.dropdown:focus {
  border: 1px solid #fff6;
}

.ui.fluid.dropdown > .dropdown.icon {
  color: #ffffffbf;
}

.ui.fluid.dropdown.active > .dropdown.icon {
  color: #fff;
}

.ui.multiple.dropdown > .text, .ui.multiple.search.dropdown > .text {
  color: #ffffffbf;
  margin: 0;
  padding: 10px 13px;
}

.ui.multiple.search.dropdown > input.search {
  color: #fff;
  box-sizing: content-box;
  margin: 0;
  padding: 10px 13px;
  font-size: 13px;
}

.ui.multiple.dropdown > .label {
  color: #fff;
  vertical-align: middle;
  background-color: #222;
  border-radius: 15px;
  margin-top: 0;
  margin-left: 5px;
  padding: 5px 10px;
}

.ui.multiple.dropdown > .label:first-of-type {
  margin-left: 10px;
}

.ui.multiple.dropdown > .label:last-of-type {
  margin-right: -10px;
}

i.icon.delete {
  margin-left: 10px;
}

i.icon.delete:before {
  content: "✕";
  color: #5c5c5c;
  padding-left: .25em;
  font-style: normal;
}

i.icon.delete:hover {
  cursor: pointer;
}

.ui.selection.active.dropdown, .ui.selection.active.dropdown:hover {
  border-color: #fff6;
}

.ui.dropdown .menu {
  background: #000;
}

.ui.selection.active.dropdown .menu {
  box-sizing: content-box;
  background-color: #000;
  border-color: #fff6;
  border-radius: 0 0 3px 3px;
  width: 100%;
  min-width: 100%;
}

.ui.selection.active.dropdown.upward .menu {
  border-radius: 3px 3px 0 0;
}

.ui.selection.active.dropdown:hover .menu {
  border-color: #fff6;
}

.ui.selection.dropdown .menu > .item {
  color: #fff;
  border-top: 0;
  margin-bottom: 0;
  padding-left: 13px;
  font-size: 13px;
}

.ui.selection.dropdown .menu > .item.bold {
  font-family: GT America Bold;
}

.ui.dropdown.selected, .ui.dropdown .menu .selected.item, .ui.dropdown .menu > .item:hover {
  background-color: #222;
}

.ui.selection.dropdown .menu > .message {
  color: #fff;
}
